/**
 * Ïù¥ÎØ∏ÏßÄ URL Í≤ÄÏ¶ù Î∞è ÏïàÏ†ÑÌïú Ï≤òÎ¶¨ Ïú†Ìã∏Î¶¨Ìã∞
 */

/**
 * ÌîÑÎ°ùÏãúÎ•º ÌÜµÌï¥ Ïù¥ÎØ∏ÏßÄ URLÏùÑ Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
 */
export function getProxiedImageUrl(originalUrl: string): string {
  try {
    const encodedUrl = encodeURIComponent(originalUrl);
    return `/api/proxy-image?url=${encodedUrl}`;
  } catch (error) {
    console.warn('üñºÔ∏è [ImageUtils] ÌîÑÎ°ùÏãú URL ÏÉùÏÑ± Ïã§Ìå®:', originalUrl, error);
    return originalUrl;
  }
}

/**
 * Google Ïù¥ÎØ∏ÏßÄ URLÏùÑ ÏïàÏ†ÑÌïú ÌòïÌÉúÎ°ú Î≥ÄÌôò
 */
export function getSafeGoogleImageUrl(url: string): string {
  try {
    const urlObj = new URL(url);
    
    // Google Ïù¥ÎØ∏ÏßÄ ÏÑúÎπÑÏä§Ïù∏ÏßÄ ÌôïÏù∏
    if (urlObj.hostname.includes('googleusercontent.com')) {
      // Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞Î•º ÏûëÍ≤å Ï°∞Ï†ïÌïòÏó¨ ÏöîÏ≤≠ Î∂ÄÌïò Í∞êÏÜå
      const searchParams = new URLSearchParams(urlObj.search);
      
      // Í∏∞Ï°¥ ÌÅ¨Í∏∞ ÌååÎùºÎØ∏ÌÑ∞ Ï†úÍ±∞ÌïòÍ≥† ÏûëÏùÄ ÌÅ¨Í∏∞Î°ú ÏÑ§Ï†ï
      if (urlObj.pathname.includes('=s') || urlObj.search.includes('sz=')) {
        // s96-c ÌòïÌÉúÏùò ÌÅ¨Í∏∞ ÌååÎùºÎØ∏ÌÑ∞Î•º s64-cÎ°ú Î≥ÄÍ≤Ω
        url = url.replace(/=s\d+-c/, '=s64-c');
        url = url.replace(/sz=\d+/, 'sz=64');
      } else {
        // ÌÅ¨Í∏∞ ÌååÎùºÎØ∏ÌÑ∞Í∞Ä ÏóÜÏúºÎ©¥ Ï∂îÍ∞Ä
        const separator = url.includes('?') ? '&' : '?';
        url = `${url}${separator}sz=64`;
      }
      
      // Ï∫êÏãú Î≤ÑÏä§ÌåÖÏùÑ ÏúÑÌïú ÌÉÄÏûÑÏä§ÌÉ¨ÌîÑ Ï∂îÍ∞Ä (ÌïòÎ£® Îã®ÏúÑ)
      const dayTimestamp = Math.floor(Date.now() / (1000 * 60 * 60 * 24));
      const separator = url.includes('?') ? '&' : '?';
      url = `${url}${separator}t=${dayTimestamp}`;
    }
    
    return url;
  } catch (error) {
    console.warn('üñºÔ∏è [ImageUtils] Google Ïù¥ÎØ∏ÏßÄ URL Î≥ÄÌôò Ïã§Ìå®:', url, error);
    return url;
  }
}

/**
 * Ïù¥ÎØ∏ÏßÄ URLÏù¥ Ïú†Ìö®ÌïúÏßÄ Í≤ÄÏ¶ù
 */
export function isValidImageUrl(url: string | null | undefined): boolean {
  if (!url || typeof url !== 'string') return false;
  
  try {
    const urlObj = new URL(url);
    
    // HTTP/HTTPSÎßå ÌóàÏö©
    if (!['http:', 'https:'].includes(urlObj.protocol)) {
      return false;
    }
    
    // Ïù¥ÎØ∏ÏßÄ ÌôïÏû•Ïûê ÎòêÎäî ÏïåÎ†§ÏßÑ Ïù¥ÎØ∏ÏßÄ ÏÑúÎπÑÏä§ ÌôïÏù∏
    const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.webp', '.svg'];
    const imageServices = [
      'googleusercontent.com',
      'graph.facebook.com', 
      'pbs.twimg.com',
      'cdn.discordapp.com',
      'avatars.githubusercontent.com',
      'platform-lookaside.fbsbx.com',
      'lh3.googleusercontent.com',
      't1.kakaocdn.net'
    ];
    
    const hasImageExtension = imageExtensions.some(ext => 
      urlObj.pathname.toLowerCase().includes(ext)
    );
    
    const isKnownImageService = imageServices.some(service => 
      urlObj.hostname.includes(service)
    );
    
    return hasImageExtension || isKnownImageService;
    
  } catch (error) {
    console.warn('üñºÔ∏è [ImageUtils] URL Í≤ÄÏ¶ù Ïã§Ìå®:', url, error);
    return false;
  }
}

/**
 * Ïù¥ÎØ∏ÏßÄ Î°úÎî© ÌÖåÏä§Ìä∏ (ÌîÑÎ¶¨Î°úÎî©)
 */
export function preloadImage(url: string): Promise<boolean> {
  return new Promise((resolve) => {
    if (!isValidImageUrl(url)) {
      resolve(false);
      return;
    }
    
    const img = new Image();
    const timeout = setTimeout(() => {
      img.onload = null;
      img.onerror = null;
      resolve(false);
    }, 5000); // 5Ï¥à ÌÉÄÏûÑÏïÑÏõÉÏúºÎ°ú Ï¶ùÍ∞Ä
    
    img.onload = () => {
      clearTimeout(timeout);
      resolve(true);
    };
    
    img.onerror = () => {
      clearTimeout(timeout);
      console.warn('üñºÔ∏è [ImageUtils] Ïù¥ÎØ∏ÏßÄ Î°úÎî© Ïã§Ìå®:', url);
      resolve(false);
    };
    
    img.src = url;
  });
}

/**
 * ÏïàÏ†ÑÌïú ÏïÑÎ∞îÌÉÄ URL Í∞ÄÏ†∏Ïò§Í∏∞
 */
export function getSafeAvatarUrl(
  avatarUrl: string | null | undefined, 
  fallbackUrl: string = '/images/default-avatar.png',
  useProxy: boolean = false
): string {
  if (!avatarUrl) {
    return fallbackUrl;
  }
  
  if (!isValidImageUrl(avatarUrl)) {
    console.warn('üñºÔ∏è [ImageUtils] Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÏïÑÎ∞îÌÉÄ URL:', avatarUrl);
    return fallbackUrl;
  }
  
  // Google Ïù¥ÎØ∏ÏßÄ URLÏù∏ Í≤ΩÏö∞ ÏïàÏ†ÑÌïú ÌòïÌÉúÎ°ú Î≥ÄÌôò
  if (avatarUrl.includes('googleusercontent.com')) {
    const safeUrl = getSafeGoogleImageUrl(avatarUrl);
    
    // ÌîÑÎ°ùÏãú ÏÇ¨Ïö© ÏòµÏÖòÏù¥ ÌôúÏÑ±ÌôîÎêú Í≤ΩÏö∞
    if (useProxy) {
      return getProxiedImageUrl(safeUrl);
    }
    
    return safeUrl;
  }
  
  return avatarUrl;
}

/**
 * React Ïª¥Ìè¨ÎÑåÌä∏Ïö© Ïù¥ÎØ∏ÏßÄ ÏóêÎü¨ Ìï∏Îì§Îü¨ (Í∞úÏÑ†Îêú Î≤ÑÏ†Ñ)
 */
export function createImageErrorHandler(
  fallbackUrl: string = '/images/default-avatar.png',
  useProxy: boolean = false
) {
  return (event: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const img = event.currentTarget;
    const originalSrc = img.src;
    
    // Ïù¥ÎØ∏ Ìè¥Î∞± Ïù¥ÎØ∏ÏßÄÏù∏ Í≤ΩÏö∞ Î¨¥Ìïú Î£®ÌîÑ Î∞©ÏßÄ
    if (img.src === fallbackUrl || img.src.includes('default-avatar')) {
      console.warn('üñºÔ∏è [ImageUtils] Ìè¥Î∞± Ïù¥ÎØ∏ÏßÄÎèÑ Î°úÎî© Ïã§Ìå®');
      return;
    }
    
    // Ïù¥ÎØ∏ ÌîÑÎ°ùÏãúÎ•º ÏÇ¨Ïö© Ï§ëÏù∏ Í≤ΩÏö∞ Ìè¥Î∞±ÏúºÎ°ú Î≥ÄÍ≤Ω
    if (img.src.includes('/api/proxy-image')) {
      console.warn('üñºÔ∏è [ImageUtils] ÌîÑÎ°ùÏãú Ïù¥ÎØ∏ÏßÄÎèÑ Î°úÎî© Ïã§Ìå®, Ìè¥Î∞±ÏúºÎ°ú Î≥ÄÍ≤Ω:', {
        original: originalSrc,
        fallback: fallbackUrl
      });
      img.src = fallbackUrl;
      return;
    }
    
    // Google Ïù¥ÎØ∏ÏßÄÏù∏ Í≤ΩÏö∞ 429 ÏóêÎü¨ ÎåÄÏùë
    if (originalSrc.includes('googleusercontent.com')) {
      console.warn('üñºÔ∏è [ImageUtils] Google Ïù¥ÎØ∏ÏßÄ Î°úÎî© Ïã§Ìå® (429 Í∞ÄÎä•ÏÑ±):', {
        original: originalSrc,
        useProxy,
        error: '429 Too Many Requests'
      });
      
      // ÌîÑÎ°ùÏãú ÏÇ¨Ïö© ÏòµÏÖòÏù¥ ÌôúÏÑ±ÌôîÎêú Í≤ΩÏö∞ ÌîÑÎ°ùÏãúÎ°ú Ïû¨ÏãúÎèÑ
      if (useProxy) {
        const proxiedUrl = getProxiedImageUrl(originalSrc);
        console.log('üñºÔ∏è [ImageUtils] ÌîÑÎ°ùÏãúÎ°ú Ïû¨ÏãúÎèÑ:', proxiedUrl);
        img.src = proxiedUrl;
        return;
      }
      
      // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ïã§Ìå®Ìïú URL Í∏∞Î°ù (Ïû¨ÏãúÎèÑ Î∞©ÏßÄ)
      try {
        const failedUrls = JSON.parse(localStorage.getItem('failed_image_urls') || '[]');
        if (!failedUrls.includes(originalSrc)) {
          failedUrls.push(originalSrc);
          // ÏµúÎåÄ 50Í∞úÍπåÏßÄÎßå Ï†ÄÏû•
          if (failedUrls.length > 50) {
            failedUrls.shift();
          }
          localStorage.setItem('failed_image_urls', JSON.stringify(failedUrls));
        }
      } catch (error) {
        // localStorage Ï†ëÍ∑º Ïã§Ìå® Ïãú Î¨¥Ïãú
      }
    } else {
      console.warn('üñºÔ∏è [ImageUtils] Ïù¥ÎØ∏ÏßÄ Î°úÎî© Ïã§Ìå®, Ìè¥Î∞±ÏúºÎ°ú Î≥ÄÍ≤Ω:', {
        original: originalSrc,
        fallback: fallbackUrl
      });
    }
    
    img.src = fallbackUrl;
  };
}

/**
 * Ïã§Ìå®Ìïú Ïù¥ÎØ∏ÏßÄ URLÏù∏ÏßÄ ÌôïÏù∏
 */
export function isFailedImageUrl(url: string): boolean {
  try {
    const failedUrls = JSON.parse(localStorage.getItem('failed_image_urls') || '[]');
    return failedUrls.includes(url);
  } catch (error) {
    return false;
  }
}

/**
 * ÏÜåÏÖú Î°úÍ∑∏Ïù∏ Ï†úÍ≥µÏûêÎ≥Ñ ÏïÑÎ∞îÌÉÄ URL Ï∂îÏ∂ú
 */
export function extractAvatarFromProvider(
  userMetadata: any, 
  provider?: string
): string | null {
  if (!userMetadata || typeof userMetadata !== 'object') {
    return null;
  }
  
  // Ï†úÍ≥µÏûêÎ≥Ñ ÌäπÎ≥Ñ Ï≤òÎ¶¨
  switch (provider) {
    case 'google':
      const googleUrl = userMetadata.picture || userMetadata.avatar_url;
      return googleUrl ? getSafeGoogleImageUrl(googleUrl) : null;
      
    case 'facebook':
      return userMetadata.picture?.data?.url || userMetadata.picture || userMetadata.avatar_url;
      
    case 'github':
      return userMetadata.avatar_url || userMetadata.picture;
      
    case 'discord':
      return userMetadata.avatar ? 
        `https://cdn.discordapp.com/avatars/${userMetadata.id}/${userMetadata.avatar}.png` :
        userMetadata.picture || userMetadata.avatar_url;
        
    case 'kakao':
      return userMetadata.picture || userMetadata.profile_image || userMetadata.avatar_url;
      
    default:
      // ÏùºÎ∞òÏ†ÅÏù∏ ÌïÑÎìúÎì§ ÏàúÏÑúÎåÄÎ°ú ÏãúÎèÑ
      const possibleFields = [
        'avatar_url',
        'picture', 
        'photo',
        'image',
        'profile_image_url',
        'profile_picture',
        'profile_image'
      ];
      
      for (const field of possibleFields) {
        const url = userMetadata[field];
        if (url && typeof url === 'string' && isValidImageUrl(url)) {
          // Google Ïù¥ÎØ∏ÏßÄÏù∏ Í≤ΩÏö∞ ÏïàÏ†ÑÌïú ÌòïÌÉúÎ°ú Î≥ÄÌôò
          if (url.includes('googleusercontent.com')) {
            return getSafeGoogleImageUrl(url);
          }
          return url;
        }
      }
      
      return null;
  }
} 